.App {
  display: grid;
  grid-template-columns: 1fr 5fr 1fr;
  grid-template-rows: 0.5fr 5fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;

  height:100vh;
  width: 100%;

  background-color: #f9f7f7;
}

.App-header {
  grid-area: 1 / 2 / 2 / 3;
  display: flex;
  justify-content: flex-end;
  
  margin-top: 20px;
  margin-bottom: 20px;

  a {
    color: #3f72af;
    outline: none;
    text-decoration: none;
  }
}

.App-wrapper {
  grid-area: 2 / 2 / 3 / 3;
}
